import React, { memo } from 'react'
import { Card as CardUI } from '../../../components'
// Na aplicao os Cards so elementos de cartes do Material UI apresentandos como a rea destinada para exibio das estatsticas carregadas
import {
  LabelStyled,
  ValueStyled,
  CardContentStyled
} from './style'


function Card({ value, label, color, gocolor }) {
  return (
    <CardUI id={gocolor} >
      <CardContentStyled color={color}>
        <LabelStyled>{label}</LabelStyled>
        <ValueStyled>{value}</ValueStyled>
      </CardContentStyled>
    </CardUI>
  )
}

export default memo(Card)